@import url(https://fonts.googleapis.com/css2?family=Caveat&family=Roboto&display=swap);
:root{
  --white:#f7f3f3;
  --black:black;

  --bg-color-1:#0b520b;
  --bg-color-2:#333333e6;
  --bg-color-3:#becf21;
  --bg-color-4:#0b213b80;
  --bg-color-5:#0b520b;
  --bg-color-6:#12243a80;

  --color-1:#7290be;
 --color-2:#cecece;
 --color-3:#f5f5f5;
 --color-4:#3333;

  --font-1: 'Caveat', cursive;
  --font-2:'Roboto', sans-serif;
}


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  font-family: var(--font-2);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

header{
  padding: 30px;
  text-align: center;
  font-size: 40px;
  background-color: #0b213b80;
  background-color: var(--bg-color-4);
  color: #f7f3f3;
  color: var(--white);
  width: 100%;
  text-transform: uppercase;

}


footer{
  width: 100%;
  bottom: 0;
  padding: 20px;
  text-align: center;
  color: #f7f3f3;
  color: var(--white);
  background-color: #0b213b80;
  background-color: var(--bg-color-4);
}

.footerWrapper{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

footer p{ 
  font-family: 'Roboto', sans-serif; 
  font-family: var(--font-2);
  font-size: 1.5rem;
  padding: 30px;
}

footer .icons i {
  margin: 20px ;
  font-size: 50px;
  color: #12243a80;
  color: var(--bg-color-6);
}

footer .icons i:hover{
  color: var(--ancient-2);
  transition: 1s;
}


/* ####### Popup Component ############### */
/* ####### Button Popup ##### */

.overlay button{
    background-color: var(--bg-color-1);
    color:var(--white);
    border: none;
    border-radius: 5px;
    box-shadow: 0 0 5px var(--color-4);
    padding: 1rem;
    width: 200px;
    font-size: 20px;
    margin-top: 2rem;
    text-transform: uppercase;
    margin: 20px;
  }
  
  .popup{
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: var(--bg-color-2);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .overlay{
    position: relative;
    top:0;
    left:0;
    background-color: var(--bg-color-3);
    color:var(--black);
    padding: 20px;
    width: 80%;
    height: 70%;
    box-shadow: 0 0 5px var(--bg-color-2);
    margin: 0 auto;
  
  }
  
  .overlay p, .overlay h1{
    padding: 1rem 0;
    font-size: 30px;
  }
  
  .overlay h1{
      text-align: center;
  }

  .overlay p span{
    margin: 50px;
    font-family: var(--font-1);
    font-size: 2.5rem;
    padding: 1rem 0;
  }
  
  .overlay .check{
    margin: 20px ;
    display:inline-block;
    text-align: center;
    width: 500px;
    text-transform: uppercase;
    background-color: var(--white);
    color: var(--black);
  }
  
  .check:hover{
    background-color: var(--bg-color-1);
    color:var(--white);
  }


/* ####### View component ##### */
.view{
  font-size: 20px;
  font-weight: bolder;
  width: 100%;
  padding: 2rem;
}
  
  .notebook{ 
  background-color: var(--color-3);
  box-shadow: 0px 0px 5px var(--color-2);
  position: relative;
  margin: 1rem 0;
  
  }
  
  
  .notebook p{
    border-bottom: 1px dashed var(--color-2);
    padding: 20px;
    text-indent: 60px;
  }
  
  .notebook::before{
    content: "";
    width: 2px;
    height: 100%;
    border-left: 1px solid var(--color-1);
    border-right: 1px solid var(--color-1);
    position: absolute;
    left: 50px;
  }
  .notebook p span{
    margin: 50px;
    font-family: var(--font-1);
    font-size: 1.5rem;
  }
  
  .view h1{
    text-align: center;
    color: var(--white);
  }
  
/* ####### Form Components ##### */
form{
    color: var(--white);
    padding: 3rem;
    font-size: 15px;
}
  
  input,
  textarea, select {
    padding: 1rem;
    border-radius: 5px;
    outline: none;
    font-size: 20px;
    width: 250px;
    font-family: var(--font-2);
    background-color: var(--color-3);
  }

  label {
    display: inline-block;
    width: 150px;
  }
  form div{
    margin: 1rem 0;
  }
  
  /* ####### input button ##### */
  form input[type="submit"]{
    background-color: var(--bg-color-4);
    color:var(--white);
    border: none;
    border-radius: 5px;
    box-shadow: 0 0 5px var(--color-4);
    font-size: 20px;
    width: 250px;
    margin-left: 150px;
    text-transform: uppercase;
    
  }
  
 .submit input[type="submit"]:hover{
   background-color: var(--color-1);
  }
  
  .formArea{
    display: flex;
    background-color:var(--bg-color-4);
    margin-top: 10px;
  }
  

  
.Notes{
 padding: 20px;
 background-color: var(--color-2);
}

.notes{
    margin-left: 5rem;
}

.note{
    margin: 10px;
    border: 1px solid var(--color-1);
    padding:20px;
    box-shadow: 0 3px 8px 7px var(--color-2), 0 6px 20px 0 ;
    width: 70%;
    background-color: var(--white);
}

.Notes p{
    margin-left: 50px;
}

.Notes p span{
    margin: 10px;
    font-family: var(--font-1);
    font-size: 1.5rem;
    padding: 1rem 0;
   
}
